* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.search-form {
  width: 100%;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  background: linear-gradient(
    180deg,
    rgba(4, 188, 239, 0.7) 45%,
    rgba(231, 228, 39, 0.7) 60%
  );
}

input {
  width: 225px;
  height: 25px;
  padding: 5px 10px;
  border-radius: 5px 0 0 5px;
  border: 1px solid #0000ff;
  border-right: none;
  outline: none;
}

.search,
.load-more {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 25px;
  border: none;
  background-color: #0000ff;
}

.search {
  padding: 0;
  width: 25px;
  border-radius: 0 5px 5px 0;
}

.load-more {
  padding: 5px 5px;
  width: 80px;
  border-radius: 5px;
  color: #fafafa;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: #fafafa;
}

.is-hidden {
  display: none;
}
